@tailwind base;
@tailwind components;
@tailwind utilities;

html,
body {
  background: #d79e67;
}

#sticker {
  filter: drop-shadow(3px 0 0 white) drop-shadow(0 3px 0 white)
    drop-shadow(-3px 0 0 white) drop-shadow(0 -3px 0 white);
}

#inkSource {
  animation: inksource 1s ease-in-out infinite;
}

@keyframes inksource {
  0% {
    width: 0.4rem;
    height: 0.8rem;
    border-radius: 50%;
  }
  50% {
    width: 0.4rem;
    height: 0.5rem;
    border-radius: 100%;
  }
  80% {
    width: 0.4rem;
    height: 0.7rem;
    border-radius: 50%;
  }
  100% {
    width: 0.4rem;
    height: 0.8rem;
    border-radius: 100%;
  }
}

#mypictures {
  background: rgb(238, 174, 202);
  background: linear-gradient(
    90deg,
    rgba(238, 174, 202, 1) 0%,
    rgba(148, 187, 233, 1) 100%
  );
}

#myprojects {
  background: rgb(131, 58, 180);
  background: linear-gradient(
    90deg,
    rgba(131, 58, 180, 1) 0%,
    rgba(253, 29, 29, 1) 50%,
    rgba(252, 176, 69, 1) 100%
  );
}

#arrowLine {
  stroke-dasharray: 228;
  stroke-dashoffset: 228;
  animation: arrowline 2.5s ease infinite;
  animation-fill-mode: forwards;
}

#arrowHead {
  stroke-dasharray: 13;
  stroke-dashoffset: 13;
  animation: arrowhead 2.5s ease infinite;
  animation-fill-mode: forwards;
}

@keyframes arrowline {
  to {
    stroke-dashoffset: 0;
  }
}

@keyframes arrowhead {
  to {
    stroke-dashoffset: 0;
  }
}

.thecontainer {
  background-color: #d79e67;
  background-image: url("./assets/background.webp");
}
.range-input::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 20px;
  height: 20px;
  border: 2px solid black;
  border-radius: 50%;
  cursor: pointer;
  background: #e5ac73;
}

.menu {
  animation: menudrop 1.5s ease-in-out 1;
  animation-fill-mode: forwards;
}

.speaker {
  animation: speakerdrop 1s ease-in-out 1;
  animation-fill-mode: forwards;
}

.volume {
  animation: volume 4s ease-in-out 1;
}

@keyframes volume {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes menudrop {
  0% {
    position: fixed;
    bottom: 500px;
    border-radius: 100%;
  }
  30% {
    position: fixed;
    bottom: 20px;
  }
  35% {
    position: fixed;
    bottom: 100px;
  }
  40% {
    position: fixed;
    bottom: 20px;
  }
  45% {
    position: fixed;
    bottom: 90px;
  }
  50% {
    position: fixed;
    bottom: 20px;
  }
  55% {
    position: fixed;
    bottom: 80px;
  }
  60% {
    position: fixed;
    bottom: 20px;
  }
  65% {
    position: fixed;
    bottom: 70px;
  }
  70% {
    position: fixed;
    bottom: 20px;
  }
  75% {
    position: fixed;
    bottom: 60px;
  }
  80% {
    position: fixed;
    bottom: 20px;
  }
  85% {
    position: fixed;
    bottom: 50px;
  }
  90% {
    position: fixed;
    bottom: 20px;
  }
  95% {
    position: fixed;
    bottom: 40px;
  }
  100% {
    position: fixed;
    bottom: 20px;
  }
}

@keyframes speakerdrop {
  0% {
    position: fixed;
    bottom: 500px;
    border-radius: 100%;
  }
  50% {
    position: fixed;
    bottom: 100px;
    margin-left: 10px;
  }
  60% {
    position: fixed;
    bottom: 150px;
  }
  100% {
    position: fixed;
    bottom: 20px;
    margin-left: 45px;
  }
}

@keyframes fade {
  0% {
    opacity: 0;
  }
  20% {
    opacity: 0.5;
  }
  30% {
    opacity: 0.7;
  }
  50% {
    opacity: 0.9;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeout {
  0% {
    opacity: 1;
  }
  20% {
    opacity: 0.9;
  }
  30% {
    opacity: 0.7;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 0;
  }
}

.hidesticker {
  display: none;
}
